<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        v-model="listQuery.name"
        placeholder="名称"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >
        搜索
      </el-button>
      <el-button
        class="filter-item"
        style="margin-left: 10px;"
        type="primary"
        icon="el-icon-edit"
        @click="handleCreate"
      >
        添加
      </el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      border
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="ID" width="95">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column label="名称" align="center">
        <template slot-scope="scope">
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label="联系人" align="center">
        <template slot-scope="scope">
          {{ scope.row.contacts }}
        </template>
      </el-table-column>
      <el-table-column label="余额" width="110" align="center">
        <template slot-scope="scope">
          <el-tag :type="scope.row.price | priceFilter">{{
            scope.row.price / 100
          }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        class-name="status-col"
        label="Status"
        width="110"
        align="center"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.status | statusFilter }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="230"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{ row }">
          <el-button
            type="primary"
            size="mini"
            @click="handleUpdate(row)"
            icon="el-icon-edit"
          >
            编辑
          </el-button>
          <el-button size="mini" @click="handleModifyStatus(row)">
            {{ 0 === row.status ? "启用" : "停用" }}
          </el-button>
          <el-button
            v-if="row.status !== '0'"
            size="mini"
            type="danger"
            @click="handlePurchase(row)"
          >
            调整余额
          </el-button>
          <router-link :to="'/tenant/' + row.id">
            <el-button size="mini">
              调整价格
            </el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="tenant"
        label-position="left"
        label-width="70px"
        style="width: 400px; margin-left:50px;"
      >
        <el-form-item
          label="名称"
          prop="name"
          v-if="dialogStatus !== 'purchase'"
        >
          <el-input v-model="tenant.name" />
        </el-form-item>
        <el-form-item
          label="名称"
          prop="name"
          v-if="dialogStatus === 'purchase'"
        >
          <el-input v-model="tenant.name" readonly="readonly" />
        </el-form-item>
        <el-form-item
          label="SID"
          v-if="dialogStatus !== 'create' && dialogStatus !== 'purchase'"
        >
          <el-input v-model="tenant.uuid" readonly="readonly" />
        </el-form-item>
        <el-form-item
          label="密钥"
          v-if="dialogStatus !== 'create' && dialogStatus !== 'purchase'"
        >
          <el-input v-model="tenant.secureKey" readonly="readonly" />
        </el-form-item>
        <el-form-item
          label="金额(元)"
          prop="price"
          v-if="dialogStatus === 'purchase'"
        >
          <el-input v-model="tenant.price" />
        </el-form-item>
        <el-form-item
          label="联系人"
          prop="contacts"
          v-if="dialogStatus !== 'purchase'"
        >
          <el-input v-model="tenant.contacts" />
        </el-form-item>
        <el-form-item
          label="手机号"
          prop="mobile"
          v-if="dialogStatus !== 'purchase'"
        >
          <el-input v-model="tenant.mobile" />
        </el-form-item>
        <el-form-item label="Remark">
          <el-input
            v-model="tenant.remark"
            :autosize="{ minRows: 2, maxRows: 4 }"
            type="textarea"
            placeholder="Please input"
          />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">
          取消
        </el-button>
        <el-button
          type="primary"
          v-if="dialogStatus !== 'purchase'"
          @click="dialogStatus === 'create' ? createData() : updateData()"
        >
          保存
        </el-button>
        <el-button
          v-if="dialogStatus === 'purchase'"
          type="primary"
          @click="purchase"
        >
          保存
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { list, get, create, update, purchase } from "@/api/tenant";
import waves from "@/directive/waves"; // waves directive
import Pagination from "@/components/Pagination";

const statusTypeOptions = [
  { value: 1, lable: "正常" },
  { value: 0, lable: "停用" }
];

// arr to obj, such as { CN : "China", US : "USA" }
const statusTypeKeyValue = statusTypeOptions.reduce((acc, cur) => {
  acc[cur.value] = cur.lable;
  return acc;
}, {});

export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    statusFilter(status) {
      return statusTypeKeyValue[status];
    },
    priceFilter(price) {
      let val = "success";
      if (price === 0) {
        val = "warning";
      } else if (price < 0) {
        val = "danger";
      }
      return val;
    }
  },
  data() {
    return {
      total: 0,
      listQuery: {
        name: "",
        pageNo: 1,
        pageSize: 20
      },
      tenant: {
        id: undefined,
        uuid: "",
        secureKey: "",
        contacts: "",
        mobile: "",
        remark: "",
        name: "",
        status: "1",
        price: 0
      },
      list: null,
      listLoading: true,
      textMap: {
        update: "编辑",
        create: "创建",
        purchase: "充值"
      },
      dialogFormVisible: false,
      rules: {
        name: [
          { required: true, message: "name is required", trigger: "change" }
        ]
      },
      dialogStatus: ""
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      list(this.listQuery).then(response => {
        this.list = response.data.record;
        this.total = response.data.total;
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.listQuery.pageNo = 1;
      this.fetchData();
    },
    resetTemp() {
      this.tenant = {
        id: undefined,
        uuid: "",
        secureKey: "",
        remark: "",
        name: "",
        status: "1",
        price: 0
      };
    },
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    handlePurchase(row) {
      this.resetTemp();
      this.tenant.id = row.id;
      this.tenant.name = row.name;
      this.dialogFormVisible = true;
      this.dialogStatus = "purchase";
    },
    handleUpdate(row) {
      get(row.id).then(response => {
        if (response.code === 0) {
          let data = response.data;
          if (data.ext) {
            this.tenant.contacts = data.ext.contacts;
            this.tenant.mobile = data.ext.mobile;
            this.tenant.remark = data.ext.description;
          } else {
            this.tenant.remark = "";
          }
          this.tenant.price = response.data.tenant.price;
          this.tenant.id = response.data.tenant.id;
          this.tenant.name = response.data.tenant.name;
          this.tenant.status = response.data.tenant.status;
          this.tenant.uuid = response.data.tenant.uuid;
          this.tenant.secureKey = response.data.tenant.secureKey;
          this.tenant.secureKey = response.data.tenant.secureKey;
          this.dialogStatus = "update";
          this.dialogFormVisible = true;
          this.$nextTick(() => {
            this.$refs["dataForm"].clearValidate();
          });
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
        }
      });
    },
    handleModifyStatus(row) {
      this.resetTemp();
      this.tenant.status = row.status === 0 ? 1 : 0;
      this.tenant.id = row.id;
      this.tenant.name = row.name;
      this.tenant.price = row.price;
      update(this.tenant).then(response => {
        if (response.code === 0) {
          this.$message({
            message: "操作成功",
            type: "success"
          });
          row.status = this.tenant.status;
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
          row.status = row.status === 0 ? 1 : 0;
        }
      });
    },
    purchase() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          let t = JSON.parse(JSON.stringify(this.tenant));
          t.price = t.price * 100;
          purchase(t).then(response => {
            if (response.code === 0) {
              for (const v of this.list) {
                if (v.id === this.tenant.id) {
                  const index = this.list.indexOf(v);
                  this.tenant.price = v.price + t.price;
                  this.list.splice(index, 1, this.tenant);
                  break;
                }
              }
              this.$notify({
                title: "成功",
                message: "充值成功",
                type: "success",
                duration: 2000
              });
              this.dialogFormVisible = false;
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
          });
        }
      });
    },
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          // this.tenant.id = parseInt(Math.random() * 100) + 1024 // mock a id
          create(this.tenant).then(response => {
            if (response.code === 1) {
              this.tenant.id = response.data;
              this.list.unshift(this.tenant);
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "创建成功",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
          });
        }
      });
    },
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          update(this.tenant).then(response => {
            if (response.code === 0) {
              for (const v of this.list) {
                if (v.id === this.tenant.id) {
                  const index = this.list.indexOf(v);
                  this.list.splice(index, 1, this.tenant);
                  break;
                }
              }
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "更新成功",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
          });
        }
      });
    }
  }
};
</script>
